/**
 * @private 
 * @module  models/mapDataModel
 */

/**
 * , . , , 
 * , .
 * 
 * , , 
 */
let mapCenterPoint = null;
/**
 * 
 */
const mapDataCache = {};

let
  /**
   * ID, 
   */ 
  plazaID = '',
  /**
   * ID, , 
   */
  buildingID = '',
  /**
   * , map, , Mapmap, 
   */
  crs = null;



function getMapData(plazaID, floor) {
  const key = `${plazaID}-${floor}`;
  return mapDataCache[key];
}
function setMapData(plazaID, floor, data) {
  const key = `${plazaID}-${floor}`;
  mapDataCache[key] = data;
}

export default {
  mapCenterPoint,
  getMapData,
  setMapData,
  plazaID,
  buildingID,
  crs
}


