/**
 * 
 * 
 * @private
 * @module utils/Util
 */

import L, { Point, CRS, Util } from 'leaflet';
import * as mapDataUtils from './mapDataFormat';
import { toOriginalCoordinates, latLngToPoint, pointToLatLng } from './transformCoordinates';

/**
 * Promise
 * 
 * @param {Object} obj 
 * @return {Boolean}
 * 
 * @memberof utils/Util#
 */
function isPromise(obj) {
  // obj instanceof Promise
  if (obj && typeof obj.then === 'function') {
    return true;
  }
  return false;
}

/**
 * , latlng. 
 * 
 * @param {Array} list 
 * @param {Function} unproject 
 * 
 * @return {Array} 
 */
function upprojectList (list, crs) {
  const latlngList = [];
  if (list && list.length > 0) {
    list.forEach(pos => {
      const latlng = transformCoordinates(pos, crs);
      latlngList.push([latlng.lat, latlng.lng]);
    });
  }
  return latlngList;
}
/**
 * , , 
 * 
 * @param {Array|LatLng} pos 
 * @param {CRS} crs 
 * @return {LatLng}
 */
function transformCoordinates(pos, crs) {
  if (pos instanceof L.LatLng) {
    return pos;
  }
  if (Util.isArray(pos)) {
    return crs.unproject(new Point(pos[0], pos[1]));
  }
  return L.latlng(pos);
}
/**
 * , feature
 * 
 * @param {FeatureGroup} featureGroup 
 * @return {Array}
 */
function getStoreFeatures(featureGroup) {
  const storeFeatureList = [];
  featureGroup.eachLayer(layer => {
    const regionType = parseInt(layer.feature.properties.re_type);
    if (isStore(regionType)) {
      storeFeatureList.push(layer);
    }
  });
  return storeFeatureList;
}
/**
 * ,  >= 60000 
 * 
 * @return {Boolean}
 */
function isStore(regionType) {
  if (regionType >= 60000) {
    return true;
  }
  return false;
}

export {
  isPromise,
  upprojectList,
  getStoreFeatures,
  isStore,
  mapDataUtils,
  toOriginalCoordinates,
  latLngToPoint,
  pointToLatLng
}


